<template>
  <div class="formLog">
    <ul :class="{'disable':true,'error':checkErrorClass(),'ok': submit.status }">
      <li v-for="(err, index) in errors" :key="`error_id_${index}`">
        {{ err }}
      </li>
    </ul>

    <form id="logManualInsert"  v-on:submit.prevent="checkForm" method="get"> 
    <ul>
        <li>Batch File</li>
        <li><input type="file" name="file" ref="file" v-on:change="handleFileUpload()"></li>

        <li class="send"><input type="submit" value="Submit" class="submitLog"></li>

    </ul>
    </form>

  </div>
</template>

<script>
export default {
  name: 'FormBatch',
  props: {
    msg: String
  },
  data(){
    return{

        dateChecker: new RegExp("(\\d{4}-\\d{2}-\\d{2} \\d{2}:\\d{2}:\\d{2}(?:.\\d{3}\\b)?)$"),
        ipChecker: new RegExp("^((25[0-5]|(2[0-4]|1[0-9]|[1-9]|)[0-9])(\\.(?!$)|$)){4}$"),

        errors:[],
        submit:{
          status: false
        },

        batchFile: null
    }
  },
  methods:{
    
    checkErrorClass: function(){
        return this.errors.length > 0;
    },

    checkForm: function (e) {
      this.errors = [];
      this.submit.status = false;


      if(this.batchFile != null){
        this.submitToApi();
      }else{
        this.errors.push("Choose a file")
      }

      e.preventDefault();
    },

    handleFileUpload: function(){
      console.log(this.$refs.file.files[0]);
      this.batchFile = this.$refs.file.files[0];
    },


    submitToApi: function(){
      this.errors = [];
      this.submit.status = false;
      let formData = new FormData();
      formData.append('file', this.batchFile);

      let headers = new Headers({
          // 'Content-Type': 'multipart/form-data'
      });

      fetch('http://localhost:8080/batch/', {
                    method: 'POST',
                    headers: headers,
                    body: formData
                }).then((response) => {
                    console.log(response);
                    if(response.status == 201){
                      this.submit.status = true;
                      this.errors.push("Batch has been executed");
                    }else{
                        this.submit.status = false;
                    }
                })
                .catch((error) => {
                    this.errors.push(error);
                    console.error(error);
                });
    }
  }
}
</script>

